<template>
  <div style="height: 100%">
    <div :spinning="loading" :tip="$t('device.tip')" style="height: 100%">
      <a-card class="card" :bordered="false" style="height: 100%">
        <a-form :form="form" class="form">
          <a-row class="form-row" :gutter="16">
            <a-col :lg="7" :md="7" :sm="24">
              <a-form-item :label="this.$t('dept.index')">
                <dept-select 
                  v-decorator="[
                    'deptId',
                    {initialValue: data.deptId?data.deptId:'',rules: [{required: true,message:$t('dept.index_null')}]}
                  ]" />
              </a-form-item>
            </a-col>
            <a-col :xl="{span: 7, offset: 1}" :lg="{span: 7, offset: 1}" :md="{span: 24}" :sm="24">
              <a-form-item
              >
                <a-upload accept=".xlsx" :showUploadList="false" :file-list="fileList" :remove="handleRemove" :before-upload="beforeUpload">
                  <a-button type="primary" icon="upload" ghost > {{ $t('button._007') }} </a-button>&nbsp;&nbsp;
                </a-upload>
                <a download="transformer.xlsx" href="/inhe-platform-mdm/loading/transformer.xlsx" class="download">{{ $t('common.download_model') }}</a>
                <br>
                <a-input v-model="xlsxFile" :readOnly="true" :allowClear="true" :placeholder="$t('common.import_xlsx')" >
                  <a-icon slot="suffix" type="delete" @click="handleRemove" v-if="fileList.length > 0"/>
                </a-input>
              </a-form-item>
            </a-col>
          </a-row>
        </a-form>
      </a-card>
    </div>
  </div>
</template>
<script>
  import Dict from '@/views/public/Dict'
  import DeptSelect from '@/views/public/DeptSelect'

  export default {
    components: {
      Dict,
      DeptSelect
    },
    data() {
      return {
        loading: false,
        isPost:false,
        url: '',
        xlsxFile:'',
        form: this.$form.createForm(this),
        data: {},
        param: {},
        fileList: [],
        deptId:'',
        substationData: {},
        formItemLayout: {labelCol: {lg: {span: 7}, sm: {span: 7}},wrapperCol: {lg: {span: 10}, sm: {span: 17}}},
      }
    },
    props: {
      params: {
        type: Object,
        default: null
      }
    },

    methods: {
      handleRemove(file) {
        console.log(file)
        this.xlsxFile=''
        const index = this.fileList.indexOf(file)
        const newFileList = this.fileList.slice()
        newFileList.splice(index, 1)
        this.fileList = newFileList
      },
      beforeUpload(file) {
        console.log(file)
        this.xlsxFile=file.name
        this.fileList = [...this.fileList, file]
        return false
      },
      onSubmit() {
        var that=this
        let result = false      
        that.form.validateFields((err, values) => {
          console.log(values)
          if (!err) {
            if(!that.fileList.length) {
              that.$message.info(that.$t('common.import_info7'))
              return
            }
            var formData = new FormData()
            formData.append('fileName',  that.fileList[ that.fileList.length-1])
            formData.append('deptId', values.deptId)
            result=formData
            console.log(result)
          }
        })
        return result
    },
      onCancel(){
        this.$emit('cancel')
      },
    }
  }
</script>
<style lang="scss" scoped>
  .card{
    margin-bottom: 24px;
  }
  .ant-upload-list{
    display: none;
  }
  .download{
    text-decoration: underline;
  }
  .ant-upload-list{
    width:100%;
    border: red;

  }
</style>
