<template>
  <div>
    <iframe ref="plugIframe" frameborder="0" :src="src" @load="initData"/>
  </div>
</template>
<script>
export default {
  data () {
    return {
      iframe: null,
      L: null,
      Map: null,
      src: process.env.NODE_ENV === 'production'?'/inhe-platform-mdm/map.html':'/map.html',
      loaded: false
    }
  },
  props: {
    width: {
      type: String,
      default: '100%'
    },
    height: {
      type: String,
      default: '100%'
    }
  },
  mounted () {
    
  },
  methods: {
    initData(){
      const that=this
      that.$nextTick(function () {   
        const plugIframe = that.$refs.plugIframe        
        console.log(plugIframe)
        plugIframe.width = that.width
        plugIframe.height = that.height
        that.iframe = plugIframe.contentWindow
        that.loaded = true
        console.log('iframe已加载完毕')
      })
    },
    action(callback){
      const that=this
      if(that.loaded){
        console.log(that.iframe.action)
        if(that.iframe.action){
          that.iframe.action(function(L, map){
            callback(L, map)
          })
        }else{
          that.initData()
        }
      }
      else{
        console.log('loaded为false')
        that.initData()
      }
    }
  },
}
</script>