<template>
  <a-modal
    title="Location"
    :visible="visible"
    :confirm-loading="false"
    @ok="onSubmit"
    @cancel="handleCancel"
    width="1000px"
  >
    <div id="refMapContainer">
      <a-card>
        <maps ref="mapPlugin" class="rfMap" :style="{cursor:mapCursor}"/>
        <a-affix :style="{ position: 'absolute', top: '20px', right: '20px',background:'ragb(156,144,134,0.5)'}">
          <a-row :gutter="2" style="width:200px">
            <a-col :span="24">
              <a-input addon-before="LO:" v-model="params.lo" @input="initMarker" />
            </a-col>
          </a-row>
          <a-row :gutter="2" style="width:200px;margin-top:5px">
            <a-col :span="24">
              <a-input addon-before="LA:" v-model="params.la" @input="initMarker" />
            </a-col>
          </a-row>
        </a-affix>
      </a-card>
    </div>
  </a-modal>
</template>

<script>
  import Maps from '@/views/public/Maps'
  export default {
      name: 'Index',
      components: {
        Maps
      },
      data() {
        return {
          placement:'bottom',
          newMarkerLng:'',
          newMarkerLat:'',
          newMarker:'',
          newMarkerInfo:{},
          AddMaskervisible: false,
          confirmLoading: false,
          mapCursor:'pointer',
          markers:'', //聚合组元素
          zoomControl:'',
          markModaltop:0,
          markModalleft:0,
          map:'',
          visible: false,
          leaflet:'',
          imgUrl:''  ,
          bounds:[],
          circle:'',
          queryParam:{},
          scaleDistance:1000,
        }
      },
      props: {
        params: {
          type: Object,
          default: null
        }
      },
      mounted(){
        
        
      },
      methods: {
        showModal() {
          this.visible = true
          var that=this
          setTimeout(function(){
            that.initMarker()
          },1000)
        },
        handleCancel() {
          this.AddMaskervisible = false
          if(this.newMarker){
            this.map.removeLayer(this.newMarker)
          }
          this.newMarker=''
          this.visible = false
        },
        initMarker(){
          var that=this
          that.$refs.mapPlugin.action(function(L, map){
           // console.log('map加载完成')
            that.zoomControl!=''?map.removeControl(that.zoomControl):''
            that.zoomControl=L.control.zoom({
              position:'bottomright'
            })
            map.addControl(that.zoomControl) 
            that.map=map
            that.leaflet=L
            that.markers!=''?map.removeLayer(that.markers):''
            that.markers = L.markerClusterGroup()
            that.bounds=[]
            var posi=[]
            var a =that.params
            if(a.la!=0&&a.lo!=0){
              posi=[a.la, a.lo]
              that.bounds.push(posi)
              var myIcon = L.divIcon({
                   html: `<img src="data:image/png;base64,${that.imgUrl}"  />`,
                  iconSize:0
              })
              var marker=L.marker([a.la, a.lo], {
                index:0,
                icon:myIcon,
                draggable: true,
                riseOnHover: true
              })
              that.markers.addLayer(marker)
               marker.on('dragend', function (event) {
                var currentmarker = event.target
                var position = currentmarker.getLatLng()
              //  console.log(position)
                that.params.lo=position.lng
                that.params.la=position.lat
              })
            }else{
               that.mapCursor='crosshair'
            }
           //  console.log('ma完成')
            map.addLayer(that.markers) 
            //点击地图添加标记
            map.on('click',function(e){
              if(that.mapCursor=='crosshair'){
                that.newMarkerLat=e.latlng.lat
                that.newMarkerLng=e.latlng.lng
                that.AddMaskervisible = true
                if(that.newMarker==''){
                  that.mapCursor=='pointer'
                  that.params.lo=that.newMarkerLng
                  that.params.la=that.newMarkerLat
                  that.initMarker()
                }
              //  console.log(that.newMarker)
              }
            })
          })
        },
       onSubmit() {
        // console.log(this.params)
         this.$emit('ok',this.params)
         this.AddMaskervisible = false
          if(this.newMarker){
            this.map.removeLayer(this.newMarker)
          }
          this.newMarker=''
         this.visible = false
       },
        
        distanceChange(distance){
          this.scaleDistance=distance
         // console.log(distance)
        },
       
        //根据圆形区域半径，确定地图缩放级别
        getZoom (distance) { 
          var zoom = ['50','100','200','500','1000','2000','5000','10000','20000','25000','50000','100000','200000','500000','1000000','2000000']//级别18到3。  
          for (var i = 0,zoomLen = zoom.length; i < zoomLen; i++) {  
              if(zoom[i] - distance > 0){  
                  return 18-i+2 //之所以会多3，是因为地图范围常常是比例尺距离的10倍以上。所以级别会增加3。  
              }  
          }
        }  
      }
  }
</script>
<style lang="scss" scoped>
.rfMap{
  margin:-8px;
  margin-top:-16px;
  min-height:300px;
  height:calc(100vh - 364px)
}


</style>