<template>
  <div style="background-color: #fff;padding:10px 20px">
    <div :spinning="loading">
      <a-form :form="form" style="margin-top: 10px">
        <a-row :gutter="16">
          <a-col :xl="{span: 6,offset: 1}" :lg="{span:6,offset: 1}" :md="12" :sm="24">
            <a-form-item :label="$t('transformer.code')">
              <a-input 
                v-decorator="[
                  'tmCode', 
                  {initialValue: data.tmCode, rules: [{required: true,message:$t('transformer.code')+' '+$t('common.notnull')}] }
                ]"
              />
            </a-form-item>
          </a-col>
          <a-col :xl="{span: 6,offset: 1}" :lg="{span:6,offset: 1}" :md="12" :sm="24">
            <a-form-item :label="$t('transformer.name')">
              <a-input 
                v-decorator="[
                  'description', 
                  {initialValue: data.description, rules: [{required: true,message:$t('transformer.name')+' '+$t('common.notnull')}] }
                ]"
              />
            </a-form-item>
          </a-col>
          <a-col :xl="{span: 6,offset: 1}" :lg="{span:6,offset: 1}" :md="12" :sm="24">
            <a-form-item :label="this.$t('dept.index')">
              <dept-select 
                @change="getId"
                v-decorator="[
                  'deptId',
                  {initialValue: data.deptId?data.deptId:'',rules: [{required: true,message:$t('dept.index_null')}]}
                ]" />
            </a-form-item>
          </a-col>
        </a-row>
        <a-row :gutter="16">
          <a-col :xl="{span: 6,offset: 1}" :lg="{span:6,offset: 1}" :md="12" :sm="24">
            <a-form-item :label="$t('transformer.info3')">
              <a-input 
                v-decorator="[
                  'capacity', 
                  {initialValue: data.capacity, rules: [{required: true,message:$t('transformer.info3')+' '+$t('common.notnull')}] }
                ]"
              />
            </a-form-item>
          </a-col>
          <a-col :xl="{span: 6,offset: 1}" :lg="{span:6,offset: 1}" :md="12" :sm="24">
            <a-form-item :label="this.$t('transformer.info1')">
              <a-input-group compact>
                <a-input 
                  style="width:80%" 
                  :disabled="true" 
                  v-decorator="[
                    'lineName',
                    {initialValue: data.lineDescription}
                  ]" />
                <span style="width:20%">
                  <a-button @click="queryProd" icon="search" style="width:100%"></a-button>
                </span>
              </a-input-group>
            </a-form-item>
          </a-col>
          <a-col :xl="{span: 6,offset: 1}" :lg="{span:6,offset: 1}" :md="12" :sm="24">
            <a-form-item :label="this.$t('common.status')">
              <dict 
                type="DEVICE_STATUS"
                table="params"
                v-decorator="[
                  'status',
                  { initialValue: data.status?[data.status]:['0'] ,rules: [{required: true,message:$t('common.status')+' '+$t('common.notnull')}] }
                ]"
              ></dict>
            </a-form-item>
          </a-col>
        </a-row>
        <a-row :gutter="16">
          <a-col :xl="{span: 6,offset: 1}" :lg="{span:6,offset: 1}" :md="12" :sm="24">
            <a-form-item :label="this.$t('transformer.type')">
              <dict 
                type="TM_TYPE"
                table="params"
                @change="getType"
                v-decorator="[
                  'loadType',
                  { initialValue: data.loadType?[data.loadType]:['0'], rules: [{required: true,message:$t('transformer.type')+' '+$t('common.notnull')}] }
                ]"
              ></dict>
            </a-form-item>
          </a-col>
          <a-col :xl="{span: 6,offset: 1}" :lg="{span:6,offset: 1}" :md="12" :sm="24">
            <a-form-item :label="$t('common.longitude')">
              <a-input 
                @input="changeLalo('0',$event)"
                v-decorator="[
                  'lo',
                  {initialValue: data.lo}
                ]"
              />
            </a-form-item>
          </a-col>
          <a-col :xl="{span: 6,offset: 1}" :lg="{span:6,offset: 1}" :md="12" :sm="24">
            <a-form-item :label="$t('common.latitude')">
              <a-input 
                @input="changeLalo('1',$event)"
                v-decorator="[
                  'la',
                  {initialValue: data.la}
                ]"
              />
            </a-form-item>
          </a-col>
          <a-col :xl="{span: 1,offset: 1}" :lg="{span:1,offset: 1}" :md="12" :sm="24">
            <span slot-scope="" style="display: inline-block;margin-top:45px;">
              <img src="@/assets/device/locationOff.png" alt="icon" class="icon" v-if="!data.la && !data.lo" @click="setPoint('1')">
              <img src="@/assets/device/locationOn.png" alt="icon" class="icon" v-if="data.la && data.lo" @click="setPoint('0')">
            </span>
          </a-col>
        </a-row>
      </a-form>
    </div> 
    <footer-tool-bar :style="{ width: '100%'}">
      <a-button @click="onCancel">{{ $t('common.btn_cancel') }}</a-button>&nbsp;&nbsp;
      <a-button @click="onSubmit" type="primary" v-if="params.opType!='3'">{{ $t('common.btn_ok') }}</a-button>
    </footer-tool-bar>
    <set-point :params="laloParams" @ok="getPoint" ref="setModal"></set-point>
    <search-line ref="selectLine" @ok="loadDeviceData"></search-line>
  </div>
</template>
<script>
  import { axios } from '@/utils/request'
  import SetPoint from '@/views/public/SetPoint'
  import Dict from '@/views/public/Dict'
  import DeptSelect from '@/views/public/DeptSelect'
  import FooterToolBar from '@/components/FooterToolbar'
  import notification from 'ant-design-vue/es/notification'
  import SearchLine from '@/views/public/SearchLine'

  export default {
    components: {
      Dict,
      FooterToolBar,
      notification,
      SetPoint,
      DeptSelect,
      SearchLine
    },
    data() {
      return {
        loading: false,
        form: this.$form.createForm(this),
        data: {status:0,loadType:0,lineId:''},
        serachParam:{},
        laloParams:{la:0,lo:0},
        visible:false,
        confirmLoading: false,
        formItemLayout: {labelCol: {lg: {span: 7}, sm: {span: 7}},wrapperCol: {lg: {span: 14}, sm: {span: 14}}}
      }
    },
    props: {
      params: {
        type: Object,
        default: null
      }
    },
    mounted(){
      this.initModel()
    },
    
    methods: {
      getId(values){
          if(this.params.opType=='1'){
            this.data.lineId=''
            this.form.setFieldsValue({'lineName': ''})
         }
          this.serachParam.deptId=values
      },
      getType(values){
          if(values[0]==1){
            this.serachParam.type='0'
          }else{
            this.serachParam.type=''
          }
          console.log(this.serachParam)
      },
      queryProd() {
        this.$refs.selectLine.showModal(this.serachParam)
      },
      loadDeviceData(item) {
        this.data.lineId = item[0]?item[0].id:''
        this.form.setFieldsValue({'lineName': item[0].description})
      },
      initModel(){
        if(this.params.opType=='2' || this.params.opType=='3'){
          this.initData()
        }else{
          this.data={status:0,loadType:0}
        }
      },
      initData(){
        const that = this
        that.loading = true
        axios({
          url:'/archives/structure/transformer/update-load',
          method: 'get',
          params: {id: that.params.id}
        }).then(response=>{
          that.loading = false
          that.data = response.result
          that.laloParams.la=response.result.la
          that.laloParams.lo=response.result.lo
          if(response.result.loadType==1){
            this.serachParam.type='0'
          }else{
            this.serachParam.type=''
          }
        })
      },
      onSubmit(){
        this.form.validateFields((err,values) => {
          if (!err) {            
            const that = this
            that.data = Object.assign(that.data,values)
            if(!that.params){
              return
            }
            that.data.status = that.data.status[0]
            that.data.loadType = that.data.loadType[0]
            that.loading = true
            axios({
              url: that.params.opType=='1'?'/archives/structure/transformer/insert':'/archives/structure/transformer/update',
              method: 'post',
              params: that.data
            }).then(result=>{
              that.loading = false
              if(result){
                this.$emit('ok',result)
              }
            })
          }
        })
      },
      setPoint() {
        this.$refs.setModal.showModal()
      },
      getPoint(parm){
          this.laloParams.la=parm.la
          this.laloParams.lo=parm.lo
          this.form.setFieldsValue({'la': parm.la})
          this.form.setFieldsValue({'lo': parm.lo})
      },
      changeLalo(type,e){
        if(type==0){
          this.laloParams.lo= e.target.value
        }else{
          this.laloParams.la= e.target.value
        } 
      },
      onCancel(){
        this.$emit('cancel')
      },
    }
  }
</script>
<style lang="scss" scoped>
  .card{
    margin-bottom: 24px;
  }
</style>
